<script>
    import StaticLocationMap from "../../components/StaticLocationMap";
    export default {
        components:{StaticLocationMap},
        data:()=>{
            return{
                currentLoc:{
                    lng:'0',
                    lat:'0'
                },
                restaurants:null,
                defaultRating:4,
                dialog:false,
                selectedRest:{
                    imgURL:'',
                    name:''
                },
                tabs:['Menu','Location','Comment']
            }
        },
        async beforeMount(){
            this.currentLoc.lng = this.$store.state.default.location.lng
            this.currentLoc.lat = this.$store.state.default.location.lat
            await this.$store.dispatch('getRestList')
            this.restaurants = this.$store.state.restaurants

            for(let i=0; i<this.restaurants.length; i++){
                let restLoc = this.restaurants[i].location
                let distance = this.distance(this.currentLoc.lat, this.currentLoc.lng, restLoc.lat, restLoc.lng)
                this.restaurants[i].distance = distance
            }

            this.restaurants.sort(function(a,b){
                return a.distance - b.distance
            })

            console.log(this.restaurants)

            let coord = await this.$utility.currentPos()
            this.currentLoc.lng = coord.lng
            this.currentLoc.lat = coord.lat
            console.log(coord)



        },
        watch:{
            'currentLoc.lng':function(newVal){
                console.log(newVal)
            }
        },
        mounted(){
            let vue = this

            setTimeout(function(){
                vue.$refs.map.initMap()
            }, 3000);
        },
        methods:{
            openDialog(index){
                this.selectedRest = this.restaurants[index]
                console.log("selected",this.selectedRest)
                this.dialog = true
            }
        }
    }
</script>

